<template>
  <div
    class="the-my-wallet-transactions"
    v-if="loaded"
  >
    <div class="the-my-wallet-transactions__list">
      <div class="the-my-wallet-transactions__transaction-main-description">
        Recent {{ history.length || HISTORY_RECENT_SIZE }} transactions
      </div>

      <template v-if="history.length">
        <div
          v-for="entry of history"
          :key="`${entry.action}${entry.actionId}`"
          class="the-my-wallet-transactions__transaction-item"
        >
          <div
            :class="{
              pending: entry.isConfirmed === null,
              success: entry.isConfirmed === true,
              error: entry.isConfirmed === false,
            }"
            class="the-my-wallet-transactions__transaction-status"
          />
          <div class="the-my-wallet-transactions__transaction-date">
            {{ dateToString(entry.createdAt) }}
          </div>

          <div class="the-my-wallet-transactions__transaction-description">{{ entryToDescription(entry) }}</div>

          <div class="the-my-wallet-transactions__transaction-amount">
            <e-big-number
              :value="entryToAmount(entry)"
              sign
              :decimals="getToken(entry.tokenAddress).decimals"
            />
            <span class="the-my-wallet-transactions__token-symbol">
            {{ getToken(entry.tokenAddress).shortName }}
          </span>
          </div>
        </div>
      </template>
      <div
        v-else
        key="empty"
        class="the-my-wallet-transactions__transaction-item"
      >
        <div class="the-my-wallet-transactions__transaction-status deactivated"/>
        <div class="the-my-wallet-transactions__no-transaction">
          No recent transaction. Deposit your first ETH.
        </div>
      </div>
    </div>

    <router-link
      v-if="history.length"
      :to="$popupRoute(POPUP_HISTORY)"
      class="the-my-wallet-transactions__transaction-history"
    >
      <e-icon :src="iconTransactionHistory"/>
      SHOW TRANSACTIONS HISTORY
    </router-link>
  </div>
</template>

<script>
/**
 * Component: Компонент отображения истории транзакций в моем кошельке
 *
 * @version 1.0.1
 */
import { BigNumber } from 'bignumber.js';
import { mapGetters } from 'vuex';
import { getHistoryRecent } from '@/api/backend';
import { routesMixin } from '@/util/mixins';
import { dateToString } from '@/util/dates';
import log from '@/util/logger';
import { DEFAULT_DISPLAYED_DECIMALS } from '@/util/constants';
import { ACTION_DEPOSIT, ACTION_WITHDRAW, ACTION_BUY, ACTION_SELL, ACTION_AFFILIATE } from '@/api/backend/constants';

// Components
import EBigNumber from '@/components/EBigNumber';
import EIcon from '@/components/EIcon';

// Icons
import iconTransactionHistory from '@/icons/e-icon_transaction-history.svg';
import toDisplayedPrice from '@/util/format/toDisplayedPrice';

export default {
  name: 'the-my-wallet-transactions',

  props: {
    /**
     * Адрес токена по которому отображается история
     */
    tokenAddress: {
      type: String,
      required: true,
    },
  },

  data() {
    return {
      loaded: false,
      history: [],
    };
  },

  computed: {
    ...mapGetters({
      getToken: 'tokens/tokenByContract',
      activeWallet: 'wallets/activeWallet',
    }),
  },

  watch: {
    activeWallet: 'loadHistory',
    tokenAddress: 'loadHistory',
  },

  created() {
    Object.assign(this, {
      iconTransactionHistory,
    });
  },

  async mounted() {
    this.loadHistory();
  },

  methods: {
    dateToString,

    /**
     * Загрузка истории транзакций
     */
    async loadHistory() {
      if (this.$store.getters['wallets/isLogin']) {
        this.loaded = false;
        await this.$store.dispatch('contracts/loadActiveExchangeContract');
        try {
          this.history = await getHistoryRecent({
            userAddress: this.$store.getters['wallets/activeWalletAddress'],
            contractAddress: this.$store.getters['contracts/activeMainContractAddress'],
            tokenAddress: this.tokenAddress,
            tokenDecimals: this.$store.getters['tokens/tokenByContractSimple'](this.tokenAddress).decimals,
          });
        } catch (error) {
          log.warn(`Failed to fetch recent history: ${error.message}`, {
            module: 'history',
            layer: 'ui',
          });
        }
      }
      this.loaded = true;
    },

    entryToAmount(entry) {
      const amount = entry.tokenAmount;
      return ({
        [ACTION_DEPOSIT]: () => amount,
        [ACTION_WITHDRAW]: () => amount.negated(),
        [ACTION_BUY]: () => amount,
        [ACTION_SELL]: () => amount.negated(),
        [ACTION_AFFILIATE]: () => amount,
      }[entry.action]());
    },

    entryToDescription(entry) {
      const token = this.getToken(entry.tokenAddress);
      return ({
        [ACTION_DEPOSIT]: () => entry.action,
        [ACTION_WITHDRAW]: () => entry.action,
        [ACTION_BUY]: () => `Buy ${
          entry.tokenAmount.shiftedBy(-token.decimals).toFixed(DEFAULT_DISPLAYED_DECIMALS, BigNumber.ROUND_DOWN)
        } ${token.shortName} @ ${
          // TODO: move to API call
          toDisplayedPrice(entry.unitPrice, token.decimals).toFixed(DEFAULT_DISPLAYED_DECIMALS, BigNumber.ROUND_DOWN)
        }`,
        [ACTION_SELL]: () => `Sell ${
          entry.tokenAmount.shiftedBy(-token.decimals).toFixed(DEFAULT_DISPLAYED_DECIMALS, BigNumber.ROUND_DOWN)
        } ${token.shortName} @ ${
          // TODO: move to API call
          toDisplayedPrice(entry.unitPrice, token.decimals).toFixed(DEFAULT_DISPLAYED_DECIMALS, BigNumber.ROUND_DOWN)
        }`,
        [ACTION_AFFILIATE]: () => entry.action,
      }[entry.action]());
    },
  },

  components: {
    EBigNumber,
    EIcon,
  },

  mixins: [routesMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-my-wallet-transactions {
  &__list {
    padding-top: 30px;
    padding-bottom: 30px;
    width: 100%;
    max-width: 830px;
  }

  &__transaction-item {
    display: flex;
    align-items: center;
    height: 38px;
  }

  &__transaction-status {
    flex: 0 0 30px;
    height: 12px;
    position: relative;

    &:before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      border: 2px solid transparent;
      background: transparent;
    }

    &.success {
      &:before {
        border-color: #1FCE44;
        background: #BAF5C6;
      }
    }

    &.error {
      &:before {
        border-color: #DA4830;
        background: rgba(218, 72, 48, .5);
      }
    }

    &.pending {
      &:before {
        border-color: #FFDE44;
        background: #FFFE57;
      }
    }

    &.deactivated {
      &:before {
        background: rgba(175,184,190,0.30);
        border-color: #aaafbc;
      }
    }
  }

  &__no-transaction {
    font-size: 12px;
    color: #8384AB;
    letter-spacing: 0;
  }

  &__transaction-date,
  &__transaction-description,
  &__transaction-amount {
    color: #8384AB;
    font-weight: 500;
    font-size: 12px;
  }

  .e-big-number {
    font-weight: 500;
    letter-spacing: 0;
    font-size: 12px;
    line-height: 1.17;
  }

  &__token-symbol {
    color: rgba(131, 132, 171, .4);
  }

  &__transaction-date {
    flex: 0 0 296px;
  }

  &__transaction-description {
    flex: 0 0 306px;
    font-size: 10px;
    text-transform: uppercase;
  }

  &__transaction-main-description {
    padding-bottom: 6px;
    color: #09168A;
    font-weight: 500;
    font-size: 13px;
  }

  &__transaction-history {
    margin: 12px 0 40px;
    cursor: pointer;
    display: flex;
    align-items: center;
    color: #0123E7;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 10px;
    font-weight: 500;
    transition: color .15s ease-in-out;
    letter-spacing: .5px;

    &:hover {
      color: #0D105B;
    }

    .e-icon {
      width: 18px;
      height: 18px;
      margin-right: 10px;
    }
  }
}
</style>



